import React from 'react'

function Faqs() {
    return (
        <div>
            <h6 className=" fw-700 text-dark">What are the uses of a Loan against Property?</h6>
            <p className="fs-14 mb-xl-20">A loan taken against a residential or commercial property is a Loan against Property and can be used for both personal and business purposes.</p>
            
            <h6 className=" fw-700 text-dark">How amount can I get as loan against property?</h6>
            <p className="fs-14 mb-xl-20">The bank providing the loan looks at your repayment capacity for calculating the loan amount. The repayment capacity is calculated by considering your income, age, qualifications, number of dependants, spouse’s income, assets, liabilities, stability and continuity of occupation and savings history. However the eligibility of loan does not, generally, exceed 60 percent of the market value of the property.</p>
            
            <h6 className=" fw-700 text-dark">Can there be a co-applicant for loan against property? If yes, who can be co-applicant?</h6>
            <p className="fs-14 mb-xl-20">Yes, there can be a co-applicant for loan against property. Your spouse can be considered as a co-applicant. Having co-applicant results in a higher amount being approved. However, if the property is co-owned, all co-owners mandatorily need to be co-applicants.</p>
            
            <h6 className=" fw-700 text-dark">What would be the processing fees for a loan against property?</h6>
            <p className="fs-14 mb-xl-20">This depends on the loan providing bank and can vary from bank to bank and is generally around 1 percent. </p>

            <h6 className=" fw-700 text-dark">What rate of interest is applicable on loan against property and how is it calculated?</h6>
            <p className="fs-14 mb-xl-20">Rate of interest is calculated on daily reducing balance. Your monthly out-go (equated monthly installment – EMI) is much lower as compared to the interest on annual reducing balance.</p>

            <h6 className=" fw-700 text-dark">What can the tenure of the loan against property be?</h6>
            <p className="fs-14 mb-xl-20">The tenure for loans against property can be a maximum of 15 years tenure, subject to the condition it does not exceed your retirement age. This condition however can be flexible in certain cases</p>

            <h6 className=" fw-700 text-dark">What are the modes to repay this loan?</h6>
            <p className="fs-14 mb-xl-20">The repayment can be through Equated Monthly Installments (EMIs) which is inclusive of principal and interest. The EMI commences from the month following the month in which the full disbursement is made.</p>

            <h6 className=" fw-700 text-dark">What is the collateral or security that needs to be provided for loan against property?</h6>
            <p className="fs-14 mb-xl-20">In this type of a loan, one needs to mortgage a property for availing the loan. You need to deposit the title deeds and/or such other collateral security. Collateral security for by way of assignment of insurance policy or any such other assignable financial instruments are also required, as security to loan if deem necessary by the Bank. There should not be any existing mortgage, loan or litigation which is likely to affect the title to the property adversely.</p>

            <h6 className=" fw-700 text-dark">Is there a prepayment or foreclosure option available for loan against property?</h6>
            <p className="fs-14 mb-xl-20">Yes. Prepayment is possible and there is no prepayment fee if you repay the loan after six months of availing the loan if you pay from your own source of funds without transferring the loan.</p>

            <h6 className=" fw-700 text-dark">How is my loan reassessed if there is a change in status from Non-Resident Indian to Resident Indian?</h6>
            <p className="fs-14 mb-xl-20">The repayment capacity of the applicant(s) based on Resident status is reassessed and a revised repayment schedule worked out. The new rate of interest will be as per the currently applicable rate of Resident Indian loans (for that specific loan product). This revised rate of interest would be applicable on the outstanding balance being converted. A letter is given to the customer confirming the change of status.</p>

        </div>
    )
}

export default Faqs
